<template>
  <div class="PharmacistEvaluationListPrint">
    <h1 class="text-center" v-if="list.userData">
      <span>
        {{ cData.company_name }} /  {{ userData.p_code }} / {{ userData.bed_name }} /
        {{ userData.name }}
      </span>
      <span>72小時藥師評估表</span>
    </h1>
    
    <v-divider class="my-2"></v-divider>

    <div v-if="checkAllNeedDataOK">
      <v-form ref="form">
        <div class="grey lighten-3 pa-2 rounded">
          <h2>評估日期:{{ list.list.date }}</h2>
        </div>

        <div class="grey lighten-3 pa-2 rounded my-2">
          <h2>基本資料</h2>
          <v-divider></v-divider>
          <v-row class="contentStyle">
            <v-col cols="3">姓名: {{ userData.name }}</v-col>
            <v-col cols="3">床號: {{ userData.bed_name }}</v-col>
            <v-col cols="3">出生日期: {{ userData.birthday }}</v-col>
            <v-col cols="3">性別: {{ userData.sex }}</v-col>
            <v-col cols="3">入住日期: {{ userData.in_date }}</v-col>
            <v-col cols="8"
              >過去病史: {{ returnuDiagnosis(userData.diagnosis) }}
            </v-col>
          </v-row>
        </div>

        <div class="grey lighten-3 pa-2 rounded my-2">
          <h2>生理狀況</h2>
          <v-divider></v-divider>
          <v-row class="contentStyle">
            <v-col cols="4"> 行動能力: {{ list.list.active }} </v-col>
            <v-col cols="4"> 特殊照護: {{ list.list.spCare ? list.list.spCare.toString() : '' }} </v-col>
            <v-col cols="4"> 藥物依賴: {{ list.list.medicineRely ? list.list.medicineRely.toString() : '' }} </v-col>
            <v-col cols="12">
              過敏史:
              <span
                v-if="
                  !(
                    userData.allergyhistory2.length +
                    userData.allergyhistory4.length +
                    userData.allergyhistory5.length
                  )
                "
                >無</span
              >
              {{ userData.allergyhistory2 }} {{ userData.allergyhistory4 }}
              {{ userData.allergyhistory5 }}
            </v-col>
          </v-row>
        </div>

        <div class="grey lighten-3 pa-2 rounded my-2">
          <h2>服用藥物</h2>
          <v-divider></v-divider>
          <div
            v-for="medical_snkey in returnMedicalArr()"
            :key="medical_snkey.snkey"
            class="my-3"
          >
            <h3 class="text-right mb-1">
              {{
                filterMatchMedical(medical_snkey)[0].medicalData.hospital_name
              }}-{{
                filterMatchMedical(medical_snkey)[0].medicalData.branch_name
              }}
              醫師:
              {{ filterMatchMedical(medical_snkey)[0].medicalData.doctor_name }}
            </h3>

            <v-simple-table>
              <thead>
                <tr>
                  <th>用藥日期</th>
                  <th>藥名</th>
                  <th>次量</th>
                  <th>頻率</th>
                  <th>途徑</th>
                  <th>天數</th>
                  <th>總量</th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for="item in filterMatchMedical(medical_snkey)"
                  :key="item.snkey"
                >
                  <td>{{ item.start_date }}</td>
                  <td>{{ item.name }} {{ item.dosage }}</td>
                  <td>{{ item.quantity }} {{ item.unit_name }}</td>
                  <td>{{ item.eat_medicine_timeData ? item.eat_medicine_timeData.name : '無資料'}}</td>
                  <td>{{ item.method_name }}</td>
                  <td>
                    <span v-if="item.eat_medicine_timeData">
                        {{isNaN(item.quantity) 
                        ? item.quantity
                        : ((item.medicine_counts /
                          returnMathFromStr(item.quantity) /
                          item.eat_medicine_timeData.time.split(",").length /
                          item.eat_medicine_timeData.day)*100).toFixed(0)/100
                        }}

                      </span>
                  </td>
                  <td>{{ item.medicine_counts }}</td>
                </tr>
              </tbody>
            </v-simple-table>
          </div>
        </div>

        <div class="grey lighten-3 pa-2 rounded my-2">
          <h2>藥師建議:</h2>
          <v-divider></v-divider>
          <h4 style="white-space: pre-wrap">{{ list.list.pharmacistSuggest }}</h4>
        </div>

        <div class="grey lighten-3 pa-2 rounded my-2">
          <h2>機構護理人員回覆:</h2>
          <v-divider></v-divider>
          <h4 style="white-space: pre-wrap">{{ list.list.nursingSuggest }}</h4>
        </div>

      </v-form>
    </div>

  </div>
</template>

<script>
import dayjs from "dayjs";
import isBetween from "dayjs/plugin/isBetween";
dayjs.extend(isBetween);

import * as math from "mathjs"; //引入mathjs,處理浮點相乘產生的問題

export default {
  data() {
    return {
      processModel: "addModel",
      emptyRules: [(v) => !!v || "不可空白"],
      list: {
        date: dayjs().format("YYYY-MM-DD"),
      },
      activeItems: ["", "獨立行走", "長期臥床", "輪椅助行", "其他輔具"],
      spCareItems: ["無", "鼻異管餵食", "使用導尿管", "使用氣切", "其他"],
      medicineRelyItems: ["有", "無"],
      userMedicineItems: null, //住民使用藥物 -> 依 uData snkey取得
      userMedicalRecordItems: null, //住民就診紀錄 -> 依 uData snkey取得
      eat_medicine_timeItems: null,
      cData: "",
      pData: "",
    };
  },

  computed: {
    //判斷資料是否都讀取OK
    checkAllNeedDataOK() {
      if (
        this.userMedicineItems != null &&
        this.userMedicalRecordItems != null &&
        this.eat_medicine_timeItems != null
      ) {
        return true;
      } else {
        return false;
      }
    },
    //住民資料
    userData() {
      return this.list.userData;
    },
    //重新整理符合目前選擇日期的藥物資料 內容: 1.建構藥物結束時間 2.選擇的日期在藥物使用區間內 3.拚出就醫資料,藥物資料 4.拚出用藥頻率
    buildMatchDayMedicines() {
      //1.
      let newMedicineItems = this.userMedicineItems.map((i) => {
        return {
          ...i,
          medicineEndDay: i.medicineEndInfo.slice(3, 13),
        };
      });

      //2.
      newMedicineItems = newMedicineItems.filter((i) =>
        dayjs(this.list.list.date).isBetween(
          i.start_date,
          dayjs(i.medicineEndDay),
          null,
          "[]"
        )
      );

      //3. 4.
      newMedicineItems = newMedicineItems.map((i) => {
        return {
          ...i,
          medicalData: this.returnMedicalData(i),
          eat_medicine_timeData: this.returnEatMedicineTime(i),
        };
      });

      return newMedicineItems;
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.list = JSON.parse(this.$route.query.data);

    this.getUserMedicine(); //取得住民使用藥物
    this.getUserMedicalRecord(); //取得住民就診紀錄
    this.getItems("eat_medicine_time"); //取得用藥
    // this.getExistData();
  },

  methods: {
    //如果已存在資料,就顯示在列表裡 -> 判斷資料是否未新增的意思
    // getExistData() {
    //   let getData = {
    //     key: "user_snkey",
    //     value: this.userData.snkey,
    //   };
    //   this.$api.options(
    //     "general/getByKey/" +
    //       this.$store.state.databaseName +
    //       "/pharmacistevalution",
    //     getData
    //   ).then((rs) => {
    //     if (rs.length) {
    //       let newItem = rs[0];
    //       this.list = JSON.parse(newItem.datalist);
    //       this.list.snkey = newItem.snkey;
    //       this.processModel = "editModel";
    //     }
    //   });
    // },
    //資料更新確認 -> 確認資料存在 -> 更新; 不存在-> 新增
    // editOK() {
    //   if (this.$refs.form.validate()) {
    //     if (this.processModel == "addModel") {
    //       console.log("add");
    //       this.list.user_snkey = this.userData.snkey;
    //       this.list.create_man =
    //         this.$store.state.pData.username +
    //         "(" +
    //         dayjs().format("YYYY-MM-DD HH:mm:ss") +
    //         ")"; //執行動作的人,時間,內容
    //       var url =
    //         "general/add/" +
    //         this.$store.state.databaseName +
    //         "/pharmacistevalution";
    //       var postData = {
    //         user_snkey: this.userData.snkey,
    //         datalist: JSON.stringify(this.list),
    //       };
    //       var msg = "資料新增";
    //     } else {
    //       console.log("edit");
    //       if (!this.list.edit_man) {
    //         this.list.edit_man = "";
    //       }
    //       this.list.edit_man =
    //         this.$store.state.pData.username +
    //         "(" +
    //         dayjs().format("YYYY-MM-DD HH:mm:ss") +
    //         ")" +
    //         this.list.edit_man; //執行動作的人,時間,內容
    //       var url =
    //         "general/edit/" +
    //         this.$store.state.databaseName +
    //         "/pharmacistevalution";
    //       var postData = {
    //         snkey: this.list.snkey,
    //         datalist: JSON.stringify(this.list),
    //         edit_man: "",
    //       };
    //       var msg = "資料已更新";
    //     }

    //     this.$api.options(url, postData).then((rs) => {
    //       if (rs.state == 1) {
    //         var pop = { msg: msg, type: true, theme: "success" };
    //         this.$store.commit("snackbar", pop);
    //         this.$emit("getAllData");
    //       } else {
    //         console.log(rs);
    //         var pop = { msg: "error", type: true, theme: "error" };
    //         this.$store.commit("snackbar", pop);
    //       }
    //     });
    //   } else {
    //     var pop = {
    //       msg: "資料輸入不完整,請再確認!!",
    //       type: true,
    //       theme: "warning",
    //     };
    //     this.$store.commit("snackbar", pop);
    //   }
    // },
    //如果內容是字串型的運算式時.透過這個去計算
    returnMathFromStr(val) {
      return math.evaluate(val);
      // math.derivative('1/4')
    },
    //回傳指定就診資料
    filterMatchMedical(snkey) {
      return this.buildMatchDayMedicines.filter(
        (i) => i.medical_snkey == snkey
      );
    },
    //回傳就診的陣列
    returnMedicalArr() {
      let newItems = this.buildMatchDayMedicines.map((i) => i.medical_snkey);
      newItems = new Set(newItems);
      newItems = [...newItems];
      return newItems;
    },
    //回傳指定藥物的就診紀錄資料
    returnMedicalData(item) {
      return this.userMedicalRecordItems.find(
        (i) => i.snkey == item.medical_snkey
      );
    },
    //回傳指定藥物的用藥頻率資料
    returnEatMedicineTime(item) {
      return this.eat_medicine_timeItems.find(
        (i) => i.snkey == item.eat_medicine_time_snkey
      );
    },
    //取得住民使用藥物
    getUserMedicine() {
      var postData = {
        key: "user_snkey",
        value: this.userData.snkey,
      };
      this.$api.options(
        //"general/getByKey/" + this.$store.state.databaseName + "/medicine_record",
        "medicinerecord/getByKey/" + this.$store.state.databaseName,
        postData
      ).then((rs) => {
        this.userMedicineItems = rs;
      });
    },
    //取得住民就醫紀錄
    getUserMedicalRecord() {
      this.$api.options(
        "medicinerecord/getByUserSnkey/" +
          this.$store.state.databaseName +
          "/" +
          this.userData.snkey
      ).then((rs) => {
        this.userMedicalRecordItems = rs;
      });
    },
    //回傳過去病史
    returnuDiagnosis(item) {
      let values = Object.values(item);
      let str = "";
      values.forEach((i, index) => {
        if (index > 0) {
          str += ",";
        }
        str += i;
      });
      return str;
    },

    //取得指定datatable資料
    getItems(datatable) {
      this.$api.options(
        "general/getAll/" + this.$store.state.databaseName + "/" + datatable
      ).then((rs) => {
        this[datatable + "Items"] = rs;
      });
    },
  },
};
</script>

<style lang="scss">
tbody {
  tr:hover {
    background-color: transparent !important;
  }
}
.PharmacistEvaluationListPrint {
  h2 {
    span {
      vertical-align: middle;
    }
  }

  .contentStyle {
    font-size: 1.2rem;
  }

  .rounded {
    border-radius: 5px;
  }

  .theme--light.v-data-table thead tr th {
    font-size: 1.1rem;
  }

  .theme--light.v-data-table tbody tr td {
    font-size: 1rem;
    height: 2rem;
  }
}
</style>